
.ds-border {
    border: solid 1px var(--grey-border);
    border-radius: var(--border-radius-lg);
}

.ds-shadow {
    box-shadow: '5px 2px 8px #c3c3c3';
}

.ds-container {
    width: 78vw;
    margin-bottom: 42px;
}

.ds-custom-row {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    width: 100%;
}
.ds-row {
    display: grid;
    flex-direction: row;
    grid-template-columns: repeat(12, 1fr);
    width: 100%;
}
.ds-col-1 {
    grid-column: span 1;
}
